.blue-box input, .blue-box textarea, .blue-box select { color: black; }

a { color: #192560; font-weight: bold; text-decoration: underline; }

a:hover, a:active { color: #394894; text-decoration: none; }

.btn.btn-primary, .btn.btn.btn-success { border-radius: 0; box-shadow: 0 3px 0 #dedede; border: 0; font-weight: bold; padding: 10px 20px; text-decoration: none; transition: background-color .15s; }

.btn.btn-primary { color: #fff; background-color: #192560; }

.btn.btn-primary:hover { background-color: #394894; }

.btn.btn-success { color: #2a2a2a; background-color: #deca1f; text-shadow: 0 1px 0 #e9db61; }

.btn.btn-success:hover { background-color: #e6d54a; }

.date .input-group-btn .btn { border-radius: 0 6px 6px 0; padding: 7px 6px 6px; border: 1px solid #ddd; }

.btn.small { padding: 5px 10px; font-size: .9em; }

.btn.large { padding: 15px 30px 13px; font-size: 1.2em; text-transform: uppercase; }

.clear { clear: both; }

.faded.ng-hide { opacity: 0; }

.faded.ng-hide-remove, .faded.ng-hide-add { display: block !important; }

.faded.ng-hide-remove { transition: all linear 600ms; }

.faded.ng-hide-add { transition: all linear 400ms; }

.app-setting-panel.ng-hide { opacity: 0; }

.app-setting-panel.ng-hide-remove, .app-setting-panel.ng-hide-add { display: block !important; }

.app-setting-panel.ng-hide-remove { transition: all linear .15s .3s; }

.app-setting-panel.ng-hide-add { transition: all linear .15s; }

.print-btn { float: right; margin-top: 10px; }

#report-output .report-title { float: left; }

.report-table-wrapper { overflow-x: auto; border: 1px solid #ddd; }

.report-table-wrapper table.table-bordered { border: 0; }

#widget-type-form { border-top: 1px solid #ddd; padding-top: 15px; clear: both; }

#report-filters { margin-bottom: 30px; }

#report-filters h4 { float: left; }

#report-filters h4 + .form-field { clear: both; }

#report-filters .btn-add { margin-top: 20px; }

#report-filters .report-filters { clear: both; }

.results { clear: both; margin-top: 30px; margin-bottom: 30px; float: left; width: 100%; }

.report-widget { padding: 20px; width: 49.25%; margin-right: 1.5%; margin-bottom: 1.5%; float: left; background-color: #f9f9f9; }

.report-widget:nth-of-type(even) { margin-right: 0; }

@media screen and (max-width: 991px) { .report-widget { width: 100%; margin-right: 0; } }

.report-widget h3 { float: left; margin-top: 0; }

.report-widget h4 { clear: left; float: left; }

.report-widget .btn.btn-primary { margin-top: 20px; }

.report-widget .filter { display: block; clear: both; font-size: .9em; font-weight: bold; font-style: italic; }

.chart-container { width: 100%; max-width: 400px; height: auto; margin-top: 30px; margin: 30px auto; }

.chart-container canvas { position: relative; }

.report-filters li, .widget-filters li, .widget-metrics li { margin-bottom: 5px; }

.filter-select select, .metric-select select { width: 70%; float: left; }

.widget-settings { clear: both; }

.round-controls { border-radius: 50%; background: white; width: 30px; height: 30px; border: 1px solid #ddd; float: left; margin-left: 10px; box-shadow: none; line-height: 30px; padding: 0; position: relative; top: 15px; }

.round-controls.collapsible-control { float: right; }

.add-filter { clear: both; float: left; }

.remove-widget { float: right; text-decoration: none; cursor: pointer; }

.save-report-btn { float: right; }

@media screen and (max-width: 991px) { .save-report-btn { float: left; width: 100%; margin-top: 30px; } }

.widget-collapsible { overflow: hidden; transition: all .15s; clear: both; margin-bottom: 15px; border-bottom: 1px solid #ddd; }

.widget-collapsible.collapsed { padding-bottom: 0px; max-height: 0; }

.widget-collapsible.expanded { padding-bottom: 15px; max-height: 999px; }

.filter-details { clear: both; float: left; margin-top: 10px; margin-bottom: 30px; width: 100%; }

.filter-details label { width: 100%; }

.widget-setting-label { font-weight: bold; font-size: 1.4em; }

.chart-add-btn { width: 100%; margin-top: 15px; }

.widget-create { font-size: .8em; padding: 10px 20px; }

.widget-create .widget-create-toggle { width: 100%; text-align: center; font-size: 1.1em; font-weight: 700; text-transform: uppercase; cursor: pointer; display: block; }

.widget-create .widget-create-controls { transition: all .25s; overflow: hidden; }

.widget-create .widget-create-controls > div { clear: both; }

.widget-create .widget-create-controls.open { max-height: 900px; margin-bottom: 10px; }

.widget-create .widget-create-controls.closed { max-height: 0; }

.widget-create .btn.small { font-size: 1.2em; }

.widget-create .form-field { padding: 10px 10px 15px; }

.widget-create .form-control { height: 21px; font-size: 12px; }

.widget-create .date .input-group-btn .btn { border-radius: 0 3px 3px 0; padding: 0px 4px; }

.widget-create .date .input-group-btn .btn i { font-size: 11px; }

.chart-metrics label { display: block; }

.activity-report-table { font-size: .85em; }

.activity-report-table th.sortable { cursor: pointer; }

.activity-report-table th.sortable span.sortorder { opacity: 0; }

.activity-report-table th.sortable:hover span.sortorder { opacity: 1; }

.activity-report-table ul { padding: 0 20px; }

.activity-report-table th, .activity-report-table td { white-space: nowrap; }

.sortorder:after { content: '\25b2'; }

.sortorder.reverse:after { content: '\25bc'; }

.report-type-option { float: left; margin-right: 40px; }

/* Sticky footer styles
-------------------------------------------------- */
html { position: relative; min-height: 100%; }

body { /* Margin bottom by footer height */ margin-bottom: 60px; }

footer { position: absolute; bottom: 0; width: 100%; /* Set the fixed height of the footer here */ height: 60px; background-color: #f5f5f5; }
